          ></div>
          <div class="card-body">
            <p class="card-text">{{ modalProduct.content }}</p>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h5" v-if="!modalProduct.price">$ {{ modalProduct.origin_price }}</div> 
              <del class="h6" v-if="modalProduct.price">åå¹ {{ modalProduct.origin_price }}</del>
              <div class="h5" v-if="modalProduct.price">ç¹å¹ {{ modalProduct.price }}</div>
            </div>
          </div>

          <select name="" class="form-control" v-model="num">
          <option :value="num" v-for="num in 10" :key="num">
