          :class="{'is-invalid': errors.has('tel')}"
          v-model="form.user.tel" 
          v-validate="'required'"
          placeholder="è«è¼¸å¥é»è©±">
          <span class="text-danger" v-if="errors.has('tel')">è«è¼¸å¥é»è©±</span>
        </div>
      
        <div class="form-group">
          <label for="useraddress">æ¶ä»¶äººå°å</label>
          <input type="text" class="form-control" name="address" id="useraddress" 
          :class="{'is-invalid': errors.has('address')}"
          v-model="form.user.address"
          v-validate="'required'"
          placeholder="è«è¼¸å¥å°å">
          <span class="text-danger" v-if="errors.has('address')">è«è¼¸å¥å°å</span>
        </div>
      
        <div class="form-group">
          <label for="comment">çè¨</label>
          <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
        </div>
        <div class="text-right">
          <button class="btn btn-danger">éåºè¨å®</button>
        </div>
