    </div>

  </div>
</template>


<script>
export default {
  data() {
    return {
      num: 1,
      isModalProduct: false,
      modalProduct : {},
      cart:{
          'carts': [],
      },
      products: [],
    //product: {},
      status:{
          loadingItem: '',
      },
      isLoading: false,
      coupon_code: '',
      form: {
        user:{
          name: '',
          email: '',
          tel: '',
          address: '',
        },
        message: ''
      },
    };
  },
  methods: {
    getProducts() {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products`;
      vm.isLoading = true;
      this.$http.get(url).then(response => {
        // console.log(response.data);
        vm.products = response.data.products;
        vm.isLoading = false;
      });
    },
    getProduct(id) {
      const vm = this;
    //   const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/product/${id}`;
    //   this.$http.get(url).then(response => {
        //     console.log(response);
    //   });
      vm.status.loadingItem = id;
      this.isModalProduct = true;
      this.setProduct(id);
    },
    setProduct(id){
        const vm = this;
        const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/product/${id}`;
        this.$http.get(url).then(response => {
        // console.log(response);        
        // vm.product = response.data.product;
        vm.status.loadingItem = '';
        vm.modalProduct = response.data.product;        
      });
    },
    addtoCart(id, qty = 1){
        const vm = this;
        const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
        vm.status.loadingItem = id;
        const cart = {
            product_id: id,
            qty
        };
        this.$http.post(url, { data: cart }).then(response => {
            console.log(response);
            vm.status.loadingItem = '';  
            vm.getCart();
            vm.isModalProduct = false;             
            vm.num = 1;
      });
    },
    getCart(){
        const vm = this;
        const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
        vm.isLoading = true;
        this.$http.get(url).then(response => {
            console.log(response);
            vm.cart = response.data.data;            
            vm.isLoading = false;
        });
    },
    removeCartItem(id){
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`;
      vm.isLoading = true;
      this.$http.delete(url).then(response => {
          console.log(response);
          vm.getCart();        
          vm.isLoading = false;
      });
    },
    addCouponCode(){
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/coupon`;
      const coupon = {
        code: vm.coupon_code
      }
      vm.isLoading = true;
      this.$http.post(url, {data: coupon}).then(response => {
          console.log(response);
          vm.getCart();        
          vm.isLoading = false;
      });
    },
    createOrder(){
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order`;      
      const order = vm.form;
      // vm.isLoading = true;
      this.$validator.validate().then((result) => {
        if (result) {
          this.$http.post(url, {data: order}).then(response => {
            console.log('è¨å®å·²å»ºç«', response);
            if(response.data.success){
              vm.$router.push(`customer_checkout/${response.data.orderId}`);
            }

            // vm.getCart();        
            vm.isLoading = false;
          });
        }else{
          console.log('è«è¼¸å¥å®æ´è³æ')
        }
      });

      
    },
    img(item) {
      if (item.image) {
        return item.image;
      } else {
        return item.imageUrl;
      }
      console.log(item);
    }
  },
  created() {
    this.getProducts();
    this.getCart();
  }
};
</script>

