<script>
export default {
    data(){
        return{
            modal1: false,
            data1:[],
            tempProduct:{},
            isLoading: false,
            pagination: {},
            status:{
                fileUploading: false,
            },
            products: [
                {
                    title: 'åé¡',
                    key: 'category',
                    width: 80
                },
                {
                    title: 'ååå',
                    key: 'title'
                },
                {
                    title: 'åå¹',
                    key: 'origin_price',
                    width: 80,
                    align: 'center'
                },
                {
                    title: 'å®å¹',
                    key: 'price',
                    width: 80,
                    align: 'center',                    
                },
                {
                    title: 'æ¯å¦åç¨',
                    key: 'is_enabled',
                    width: 100,
                    align: 'center',
                    render: (h, {row}) => {
                        const is_enabled = row.is_enabled
                        return is_enabled === 1 ? <div class="enabled_yes">å·²åç¨</div> : <div class="enabled_no">æªåç¨</div>
                    }
                },
                {
                    title: 'ç·¨è¼¯',
                    key: '',
                    width: 200,
                    align: 'center',
                    render: (h, params) => {
                        return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'default',
                                        // size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },                                    
                                    on: {
                                        click: () => {
                                            // this.show(params.index)
                                            this.openModal(false, params.row);
                                            console.log(params.index)
                                        }
                                    }
                                }, 'ç·¨è¼¯'),
                                h('Button', {
                                    props: {
                                        type: 'default',
                                        // size: 'small'
                                    },
                                    style: {
                                        // color: '#000'
                                    }, 
                                    on: {
                                        click: () => {
                                            // console.log(this.removeProduct)
                                            this.removeProduct(params.row);
                                        }
                                    }
                                }, 'åªé¤')                                
                            ]);
                    }
                },

            ],
        }
    },
    methods:{    
        openModal(isNew, item){
            
            if(isNew){
                this.tempProduct = {};
                this.isNew = true;
            }else{
                this.tempProduct = Object.assign({}, item);
                this.isNew = false;
            }
            this.modal1 = true;
        },
        getProducts(page){
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products?page=${page}`;
            const vm = this;            
            // console.log(process.env.VUE_APP_APIPATH, process.env.VUE_APP_CUSTOMPATH);
            vm.isLoading = true;
            this.$http.get(api).then((response) => {    
            // console.log(response.data);   
            vm.isLoading = false;
            vm.data1 =  response.data.products;
            vm.pagination = response.data.pagination;        
            })
        },
        ok () {
            this.updatedProduct();
            this.$Message.info('æåå»ºç«åå');
        },
        cancel () {
            this.$Message.info('æåæ¶äº');
        },
        updatedProduct(){
            let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product`;
            let httpMethod = 'post';
            const vm = this;
            if(!vm.isNew){
                api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
                httpMethod = 'put';
            }
            console.log(process.env.VUE_APP_APIPATH, process.env.VUE_APP_CUSTOMPATH);
            this.$http[httpMethod](api, {data: {
                ...this.tempProduct,
                image: this.tempProduct.imageUrl,           
            } }).then((response) => {    
            console.log(response.data);   
            // vm.data1 =  response.data.products;
            vm.getProducts();
            })
        },
        removeProduct(item){
            let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${item.id}`;
            this.$http.delete(api).then((response) => {    
            const vm = this;
            vm.getProducts();
            })
        },
        uploadFile(){
            console.log(this);
            const uploadedFile = this.$refs.files.files[0];
            const vm = this;
            const formData = new FormData();
            formData.append('file-to-upload', uploadedFile);
            const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/upload`;
            vm.status.fileUploading = true;
            this.$http.post(url, formData, {
                headers:{
                    'Content-Type': 'multipart/form-data'
                }
            }).then((response) => {    
                console.log(response.data);
                vm.status.fileUploading = false;
                if(response.data.success){
                    // vm.tempProduct.imageUrl = response.data.imageUrl;
                    // console.log(vm.tempProduct.imageUrl);
                    vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl)
                }else{
                    this.$bus.$emit('message:push', response.data.message, 'danger');
                }
            })
        }
    },
    computed: {
        totalPages(){
            let vm = this;
            let total = vm.products.length * vm.pagination.total_pages;
            return total;            
        }
    },
    created(){
        this.getProducts();
    }
}
</script>

